import React, { useEffect, useState } from 'react';
import './EmployeeTable.css'
import trash from './icons/trash.png'
import edit from './icons/edit.png'
import {Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap'
import { useNavigate } from "react-router-dom";

window.onload = function () {
    window.scrollTo(0, 0);
  }

function EmployeeTable({empList, setVal}) {
    // console.log(empList)
    const[clickedData, setClickedData] = useState(empList)
    const[modal, setModal] = useState(false)

   
    const navigate = useNavigate()

    const details = (empId) => {
        setModal(true)
        setClickedData(empList.find((emp) => emp.id === empId))
    }

    const deleteEmp = (clickedData) =>{
        // console.log(clickedData)
        const ind = empList.indexOf(clickedData)
        empList.splice(ind,1)
        setModal(false)
    }
    useEffect(()=>{
      setVal(clickedData)
    },[empList])

    const editEmp = () => {
        navigate("/editemptbl")
    }

    const toggle = () => {
      navigate("/")
    }
    // console.log(clickedData)
    return (
        <>
        <Modal
        size='1g'
        isOpen={modal}
        toggle={()=>setModal(!modal)}
        >
            <ModalHeader toggle={()=>setModal(!modal)}>
                <div>Employee Details</div>
            </ModalHeader>
            <ModalBody>
                <h1 className="m-heading">{clickedData.firstName} {clickedData.lastName}</h1>
                <h5 className="m-heading">{clickedData.title}</h5>
                <p className="m-heading">{clickedData.unit}</p>
                <p className="m-heading">{clickedData.bio}</p>
                <p className="m-heading">{clickedData.email}</p>
                <p className="m-heading">{clickedData.phone}</p>
            </ModalBody>
            <ModalFooter>
                <img src={edit} onClick={editEmp} className="edit" alt="edit"/>
                <img src={trash} onClick={()=>deleteEmp(clickedData)} className="del" alt="delete"/>
            </ModalFooter>
        </Modal>
        <h1 className='emp-heading'>EMPLOYEE DATA TABLE</h1>
        <div className='tbl-container'>
        <table className="table">
            <thead className='thead'>
              <tr>
                <th scope="col">#</th>
                <th scope="col">First</th>
                <th scope="col">Last</th>
                <th scope="col">Title</th>
                <th scope="col">Unit</th>
                <th scope="col">Email</th>
                <th scope="col">Phone</th>
              </tr>
            </thead>
            <tbody>
                {empList.map((emp,i)=>
              <tr key={i}>
                <th className='id' scope="row"><h4 onClick={()=>details(emp.id)}>{emp.id}</h4></th>
                <td>{emp.firstName}</td>
                <td>{emp.lastName}</td>
                <td>{emp.title}</td>
                <td>{emp.unit}</td>
                <td>{emp.email}</td>
                <td>{emp.phone}</td>
              </tr>)}
            </tbody>
        </table>
        </div>
        <button onClick={toggle} style={{width: '200px', position:"absolute", left: "40%", backgroundColor:"green", border:"none"}}>Switch to grid</button>
        </>
    );
}

export default EmployeeTable;