import './EditEmployee.css'
import React from 'react';
import {  useNavigate } from 'react-router-dom';


function EditEmployeeTbl({empList, val, setVal}) {
    
    // const[val, setVal] = useState({id:0,firstName: "", lastName: "", title: "", unit: "", email: "",bio: "", phone: ""})
    // const[val, setVal] = useState({ firstName: "", lastName:""})
    // useEffect(()=>{
    //     setVal({id:data.id,firstName: data.firstName, lastName: data.lastName, title: data.title, unit: data.unit, email: data.email,bio: data.bio, phone: data.phone})
    // }, [])
    
    const handleChange = (key, value) => setVal({ ...val, [key]: value })
    const navigate = useNavigate()
    const edit = (e) =>{
        var index = empList.findIndex(p => p.id === val.id);
        console.log(index)
        // console.log("clicked")
        if (window.confirm("Confirm Edit")) {
        console.log(val)
        // console.log(data.id)

        empList[index] = val
        navigate("/emptbl")
        e.preventDefault()
        }else{
            e.preventDefault()
        }
    }
    const back = (e) => {
        e.preventDefault()
        navigate("/emptbl")
    }
    return (
        <>
        <h1 className='heading-add'>Edit Employee Details</h1>
        <form className='add-form'>
            <div className="row mb-4">
                <div className="col">
                <div className="form-outline">
                    <input type="text" value={val.firstName} placeholder='Enter First Name' onChange={(e)=>handleChange('firstName',e.target.value)} id="form6Example1" className="form-control" required/>
                    <label className="form-label" htmlFor="form6Example1">First name</label>
                </div>
                </div>
                <div className="col">
                <div className="form-outline">
                    <input type="text" value={val.lastName} placeholder='Enter Last Name' onChange={(e)=>handleChange('lastName',e.target.value)} id="form6Example2" className="form-control" required/>
                    <label className="form-label" htmlFor="form6Example2">Last name</label>
                </div>
                </div>
            </div>

            <div className="form-outline mb-4">
                <input type="text" value={val.title} placeholder='Enter Title' onChange={(e)=>handleChange('title',e.target.value)} id="form6Example3" className="form-control" required/>
                <label className="form-label" htmlFor="form6Example3">Title</label>
            </div>

            <div className="form-outline mb-4">
                <input type="text" value={val.unit} placeholder='Enter Unit Name' onChange={(e)=>handleChange('unit',e.target.value)} id="form6Example4" className="form-control" required/>
                <label className="form-label" htmlFor="form6Example4">Unit</label>
            </div>

            <div className="form-outline mb-4">
                <input type="email" value={val.email} placeholder='Enter Email ID' onChange={(e)=>handleChange('email',e.target.value)} id="form6Example5" className="form-control" required/>
                <label className="form-label" htmlFor="form6Example5">Email</label>
            </div>

            <div className="form-outline mb-4">
                <input type="number" value={val.phone} placeholder='Enter Contact Number' onChange={(e)=>handleChange('phone',e.target.value)} id="form6Example6" className="form-control" required/>
                <label className="form-label" htmlFor="form6Example6">Phone</label>
            </div>

            <div className="form-outline mb-4">
                <textarea className="form-control" value={val.bio} placeholder='Enter additional details about the employee' onChange={(e)=>handleChange('bio',e.target.value)} id="form6Example7" rows="4"></textarea>
                <label className="form-label" htmlFor="form6Example7">Bio</label>
            </div>

            <div className="form-check d-flex justify-content-center mb-4">
                <input className="form-check-input me-2" type="checkbox" value="" id="form6Example8" />
                <label className="form-check-label" htmlFor="form6Example8"> All above details are correct and employee accepted all Terms and Conditions of the Organization. </label>
            </div>

            <button type="submit" onClick={edit} className="btn btn-primary btn-block mb-4">Confirm Edit</button>
            <button onClick={back} className="back-btn btn btn-primary btn-block mb-4">Cancel</button>
        </form>
        </>
    );
}

export default EditEmployeeTbl;