import React, { useState } from 'react';
import './Cards.css'
import Navbar from "./Navbar";
import { Link, useNavigate } from 'react-router-dom';
import { CSVLink } from 'react-csv';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import excel from '../src/icons/excel.png'
import pdf from '../src/icons/pdf.png'
import table from '../src/icons/table.png'
import next from '../src/icons/next.png'
import pre from '../src/icons/previous.png'
import employee from '../src/icons/employee.png'


window.onbeforeunload = function () {
    window.scrollTo(0, 0);
  }

function Cards({empList, setData, print}) {
    const [page, setPage] = useState(1)

    let navigate = useNavigate()

    const tblLoad = () => {
        navigate("emptbl")
    }

    let totalPages = Math.ceil(empList.length / 8)
    // console.log(totalPages)
    // const columns = [
    //     {title:"Sr", field: "id"},
    //     {title:"First Name", field: "firstNname"},
    //     {title:"Last name", field: "lastName"},
    //     {title:"Title", field: "title"},
    //     {title:"Unit", field: "unit"},
    //     {title:"Email", field: "email"},
    //     {title:"Phone", field: "phone"}
    // ]

    const updateDownPage = (value) => {
        if(page !== 1){
            setPage(page + value)
        }
      }
    const updateUpPage = (value) => {
        if(page !== totalPages){
            setPage(page + value)
        }
      }

    const downloadFun = () => {
        // console.log("hi")
        const doc = new jsPDF()
        // doc.text("Student details",20,20)
        doc.autoTable({
            body:print
        })
        doc.save('table.pdf')
    }

    const click = (empId) => {
        const clickedData = empList.find((emp) => emp.id === empId)
        setData(clickedData)
        // console.log(post)
    }
    return (
        <>
        <Navbar/>
        <CSVLink data={empList} className='btn export'><img className='excel' src={excel} alt='icon'></img></CSVLink>
        <Link to="/addEmployee"><img alt='emp icon' src={employee} className='add'></img></Link>
        <img alt='icon table' onClick={tblLoad} className='table-icon' src={table}></img>
        <div className='card-container'>
        <img onClick={downloadFun} src={pdf} alt="pdf icon" className='pdf-icon'/>
        {empList.map((emp,i) => (i >= 8 * (page - 1) && i < (8 * page)) &&
        <div key={i} className="card">
            <img className='img' src="https://cdn3.iconfinder.com/data/icons/web-ui-3/128/Account-2-1024.png" alt="John"/>
            <h1>{emp.firstName}</h1>
            <p className="title">{emp.title}</p>
            <p><i>{emp.unit}</i></p>
            <div>{emp.email}</div>
            <p className='contact'>Contact: {emp.phone}</p>
            <Link to="/details"><button to="/details" onClick={()=>click(emp.id)}>Details</button></Link>
        </div>)}
        <div className='btn-container'>
        <img src={pre} alt='pre icon' className='pre' onClick={() => updateDownPage(-1)}></img>
        <img src={next} alt='next icon' className='next' onClick={() => updateUpPage(1)}></img>
        </div>
        </div>
        </>
    );
}

export default Cards;

