import React, { useEffect } from "react";
import Styles from "./Details.module.css";
import Navbar from "./Navbar";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";

window.onbeforeunload = function () {
  window.scrollTo(0, 0);
}


function Details({ data, empData}) {
  // console.log(data);
  useEffect(()=>{
    window.scrollTo(0,0)
  },[])
  const navigate = useNavigate()
  const deleteFunction = () => {
    const ind = empData.indexOf(data)
    // console.log(ind)
    empData.splice(ind, 1)
    alert("Employee details deleted successfully!!")
}
const editFunction = () =>{
  navigate("/editEmployee")
}
  return (data?
    <>
    <Navbar/>
    <div className={Styles.detailsContainer}>
        <Link to="/"><button className={Styles.back}>Go Back</button></Link>
        <Link to="/"><button onClick={deleteFunction} className={Styles.delete}>Delete</button></Link>
        <button onClick={editFunction} className={Styles.edit}>Edit</button>
      <div>
        <img
          className={Styles.profileImg} alt="profile pic"
          src="https://cdn3.iconfinder.com/data/icons/web-ui-3/128/Account-2-1024.png"
        />
      </div>
      <div className={Styles.rightContent}>
        <h4 className={Styles.heading}>{data.firstName} {data.lastName}</h4>
        <div className={Styles.titleDetails}>{data.title}</div>
        <div className={Styles.unit}>{data.unit}</div>
        <div className={Styles.mail}>Email: <i>{data.email}</i></div>
        <p className={Styles.para}>{data.bio}</p>
        <h6 className={Styles.contactDetails}>Contact: <i>{data.phone}</i></h6>
      </div>
    </div>
    </>:""
  );
}

export default Details;
