function App() {
  
  const[data, setData] = useState()
  const[val, setVal] = useState({id:0,firstName: "", lastName: "", title: "", unit: "", email: "",bio: "", phone: ""})
  // console.log(data)
  return (
    <>
    <HashRouter>
    <Routes>
      <Route path="/" element={<Cards print={Print} setData={setData} empList = {EMPLOYEE}/>}></Route>
      <Route path="/details" element={<Details empData={EMPLOYEE} data = {data}/>}></Route>
      <Route path="/details/:id" element={<Details empData={EMPLOYEE} data = {data}/>}></Route>
      <Route path="/addEmployee" element={<NewEmployee empList={EMPLOYEE}/>}></Route>
      <Route path="/editEmployee" element={<EditEmployee data = {data} empList={EMPLOYEE}/>}></Route>
      <Route path="/emptbl" element={<EmployeeTable setVal={setVal} empList={EMPLOYEE}/>}></Route>
      <Route path="/editemptbl" element={<EditEmployeeTbl val={val} setVal={setVal} empList={EMPLOYEE}/>}></Route>
    </Routes>
    </HashRouter>
    </>
  );
}

export default App;
