import './NewEmployee.css'
import React, { useState } from 'react';
import {  useNavigate } from 'react-router-dom';



function NewEmployee({empList}) {
    const[val, setVal] = useState({id:(empList.length)+1, firstName: "", lastName: "", title: "", unit: "", email: "",bio: "", phone: ""})
    // const[val, setVal] = useState({ firstName: "", lastName:""})
    const handleChange = (key, value) => setVal({ ...val, [key]: value })
    const navigate = useNavigate()
    // console.log(empList.length)
    const submit = (e) => {
        // console.log("Clicked")
        if(val.firstName === ""|| val.lastName===""||val.title===""||val.unit===""||val.email===""||val.phone===""){
            alert("Please fill all the details")
        }else{
            e.preventDefault()
            empList.push(val)
            // console.log(empList)
            alert("Employee details added successfully!!")
            navigate("/")
        }
    }
    return (
        <>
        <h1 className='heading-add'>Add New Employee</h1>
        <form action='#' method='POST' className='add-form'>
            <div className="row mb-4">
                <div className="col">
                <div className="form-outline">
                    <input type="text" placeholder='Enter First Name' onChange={(e)=>handleChange('firstName',e.target.value)} id="form6Example1" className="form-control" required/>
                    <label className="form-label" htmlFor="form6Example1">First name</label>
                </div>
                </div>
                <div className="col">
                <div className="form-outline">
                    <input type="text" placeholder='Enter Last Name' onChange={(e)=>handleChange('lastName',e.target.value)} id="form6Example2" className="form-control" required/>
                    <label className="form-label" htmlFor="form6Example2">Last name</label>
                </div>
                </div>
            </div>

            <div className="form-outline mb-4">
                <input type="text" placeholder='Enter Title' onChange={(e)=>handleChange('title',e.target.value)} id="form6Example3" className="form-control" required/>
                <label className="form-label" htmlFor="form6Example3">Title</label>
            </div>

            <div className="form-outline mb-4">
                <input type="text" placeholder='Enter Unit Name' onChange={(e)=>handleChange('unit',e.target.value)} id="form6Example4" className="form-control" required/>
                <label className="form-label" htmlFor="form6Example4">Unit</label>
            </div>

            <div className="form-outline mb-4">
                <input type="email" placeholder='Enter Email ID' onChange={(e)=>handleChange('email',e.target.value)} id="form6Example5" className="form-control" required/>
                <label className="form-label" htmlFor="form6Example5">Email</label>
            </div>

            <div className="form-outline mb-4">
                <input type="number" placeholder='Enter Contact Number' onChange={(e)=>handleChange('phone',e.target.value)} id="form6Example6" className="form-control" required/>
                <label className="form-label" htmlFor="form6Example6">Phone</label>
            </div>

            <div className="form-outline mb-4">
                <textarea className="form-control" placeholder='Enter additional details about the employee' onChange={(e)=>handleChange('bio',e.target.value)} id="form6Example7" rows="4"></textarea>
                <label className="form-label" htmlFor="form6Example7">Bio</label>
            </div>

            <div className="form-check d-flex justify-content-center mb-4">
                <input className="form-check-input me-2" type="checkbox" value="" id="form6Example8" />
                <label className="form-check-label" htmlFor="form6Example8"> All above details are correct and employee accepted all Terms and Conditions of the Organization. </label>
            </div>

            <button type="submit" onClick={submit} className="btn btn-primary btn-block mb-4">Add Employee</button>
        </form>
        </>
    );
}

export default NewEmployee;